import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem,
         Button, Modal, ModalHeader, ModalBody, Label, Col, Row } from 'reactstrap'; 
import { Link } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components'

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);


    function RenderDish({dish}) {
		if(dish != null) {
			return(
                <FadeTransform in 
                    transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
    				<Card>
    					<CardBody>
                            <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
    						<CardTitle>{dish.name}</CardTitle>
    						<CardText>{dish.description}</CardText>
    					</CardBody>
    				</Card>
                </FadeTransform>
			);
		}
		else
		{
			return(
				<div></div>
			);
		}
	}

	function RenderComments({comments, postComment, dishId}) {
		if (comments === null || comments === undefined) {
            return <div> </div>
        }
        else {
            return(
                <div className="container">
                    <h4>Comments</h4>
                    <ul className="list-unstyled">  
                        <Stagger in >
                            {comments.map ((comment) => {
                                return (
                                    <Fade in> 
                                        <li key={comment.id}>
                                            <p>{comment.comment}</p>
                                            <p>--{comment.author}, {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}</p>
                                        </li>
                                    </Fade>
                                );
                            } ) }
                        </Stagger>
                        </ul>
                    <CommentForm dishId={dishId} postComment={postComment} />
                </div>
            );
        }
    }

    const DishDetail = (props) => {
        if(props.isLoading) {
            return(
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            );
        }
        else if(props.errMess) {
            return(
                <div className="container">
                    <div className="row">
                        <h4>{props.errMess}</h4>
                    </div>
                </div>
            );
        }

        const dish = props.dish;
        if (dish === null || dish === undefined) {
           return(
                <div></div>
            );
        }
        else
        {
            return(
                <div className="container">
                    

                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                            <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                        </Breadcrumb>
                        <div className="col-12">
                            <h3>{props.dish.name}</h3>
                            <hr />
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-12 col-md-5 m-1">
                            <RenderDish dish={props.dish} /> 
                        </div>
                        <div className="col-12 col-md-5 m-1">
                            <RenderComments comments={props.comments} 
                                postComment={props.postComment} 
                                dishId={props.dish.id} />
 
                        </div>
                    </div>
                    
                </div>
            );
        }
    }




    class CommentForm extends Component {

        constructor(props) {
            super(props);
            this.state = {
                isModalOpen: false
            };
            this.toggleModal = this.toggleModal.bind(this);
            this.commentSubmit = this.commentSubmit.bind(this);

        }

        toggleModal() {
                this.setState({
                isModalOpen: !this.state.isModalOpen
            });
        }

        commentSubmit(values) {
        this.toggleModal();
            this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);   
        }

        render() {
            return(
                <div>
                    <Button outline onClick={this.toggleModal}>
                        <span className="fa fa-pencil fa-lg"></span> Submit Comment
                    </Button>
                    <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                        <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                        <ModalBody>
                            <LocalForm onSubmit={(values) => this.commentSubmit(values)}>
                                <Row className="form-group">
                                    <Col sm={12}> <Label htmlfor="rating"> Rating </Label> </Col>
                                    <Col sm={12}>
                                        <Control.select model=".rating" name="rating"
                                        className="form-control">
                                            <option>1</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                        </Control.select> 
                                    </Col>
                                </Row>

                                <Row className="form-group">
                                    <Col sm={12}> <Label htmlfor="author">Your Name</Label> </Col>
                                    <Col sm={12}>
                                        <Control.text model=".author" id="author" name="author" 
                                        className="form-control"
                                        placeholder="Your Name"
                                            validators={{
                                                required,  minLength: minLength(3), maxLength: maxLength(15)
                                            }}
                                        />
                                        <Errors
                                            className="text-danger"
                                            model=".author"
                                            show="touched"
                                            messages={{
                                                required: 'Required ',
                                                minLength: 'Must be grater than 2 characters',
                                                maxLength: 'Must be characters 15 or less'
                                            }}
                                        />
                                    </Col>
                                </Row>

                                <Row className="form-group">
                                    <Col sm={12}> <Label htmlfor="comment">Comment</Label> </Col>
                                    <Col sm={12}>
                                        <Control.textarea model=".comment" id="comment" name="comment" 
                                        className="form-control" rows="5"
                                        />
                                    </Col>
                                </Row>

                                <Button type="submit" value="submit" color="primary">Submit</Button>
                            </LocalForm>
                        </ModalBody>
                    </Modal>
                </div>
            );
        }
    }




export default DishDetail;